import React, { useContext } from 'react'
import { AppContext } from '../Context/ContextProvider'


const ResultProgressBar = () => {
    const { quizs, showActiveQues } = useContext(AppContext)
    return (
        <div className="col-sm-8">
            <h2>Progress:</h2>
            <div className="btn-toolbar">
                {quizs.map(x => <button className={x.correct ? "btn btn-info ml-2 mb-2" : "btn btn-danger ml-2 mb-2"}
                    onClick={() => showActiveQues(quizs.indexOf(x))}>
                    <i className={x.correct ? "fa fa-check" : "fa fa-times"}></i>
                </button>
                )}
            </div>
        </div>
    )
}
export default ResultProgressBar