import React, { useContext } from 'react'
import '../css/Answer.css'
import '../css/Image.css'
import { AppContext } from '../Context/ContextProvider'

const ResultAnswer = ({ ques, indexQues, indexAns }) => {
    const { ansData } = useContext(AppContext)

    const renderContent = () => {
        if (ques.type == "text") {
            if (ques.selected === ansData[indexQues]) { /* if the answer if correct*/
                if (ques.selected === indexAns) {
                    return <h4 className='answer bg-info'> {ques.possibilities[indexAns].answer}</h4>
                }
                else {
                    return <h4 className='answer'> {ques.possibilities[indexAns].answer}</h4>
                }
            }
            else { /* if the answer is incorrect*/
                if (ansData[indexQues] === indexAns) {
                    return <h4 className='answer bg-info'> {ques.possibilities[indexAns].answer}</h4>
                }
                else if (ques.selected === indexAns) {
                    return <h4 className='answer bg-danger'> {ques.possibilities[indexAns].answer}</h4>
                }
                else {
                    return <h4 className='answer'> {ques.possibilities[indexAns].answer}</h4>
                }
            }
        }
        else { /* if image type */
            if (ques.selected === ansData[indexQues]) { /* if the answer if correct*/
                if (ques.selected === indexAns) {
                    return <div className="image-answer image-selected">
                                <img src={ques.possibilities[indexAns].answer} />
                           </div> 
                }
                else {
                    return <div className="image-answer">
                                <img src={ques.possibilities[indexAns].answer} />
                           </div> 
                }
            }
            else { /* if the answer is incorrect*/
                if (ansData[indexQues] === indexAns) {
                    return <div className="image-answer image-selected">
                                <img src={ques.possibilities[indexAns].answer} />
                           </div> 
                }
                else if (ques.selected === indexAns) {
                    return <div className="image-answer image-danger">
                                <img src={ques.possibilities[indexAns].answer} />
                           </div> 
                }
                else {
                    return <div className="image-answer">
                                <img src={ques.possibilities[indexAns].answer} />
                           </div> 
                }
            }
        }
    }
    return (
        <div className="col-sm-6">
            {renderContent()}
        </div>
    )
}
export default ResultAnswer