import React, { useState, useContext } from 'react';
import Modal from "react-bootstrap/Modal";
import { AppContext } from '../Context/ContextProvider';
import '../css/Modal.css'


const BuildingModalInfo = () => {

    const { isOpen, toggle, item } = useContext(AppContext)

    return (
        <Modal show={isOpen} onHide={toggle}>
            <Modal.Header>
                <Modal.Title>{item.type}</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <div style={{ marginBottom: 15 }} className="row">
                    <div className="col-md-8 mx-auto">
                        <img src={item.image_url} className="rounded img-fluid card-image" />
                    </div>
                </div>
                <div className="row">
                    <div className="col-md-6">
                        <p><strong>Location: </strong>{item.locations}</p>
                        <p><strong>Built: </strong>{item.built}</p>
                    </div>
                    <div className="col-md-12">
                        <p>{item.description}</p>
                    </div>
                </div>
            </Modal.Body>
            <Modal.Footer>
                <button className="btn btn-danger" onClick={toggle}>Close</button>
            </Modal.Footer>
        </Modal>
    )
}

export default BuildingModalInfo