import React, { useContext } from 'react'
import { AppContext } from '../Context/ContextProvider'
import BuildingItem from '../FactCategory/BuildingItem'

const BuildingHome = () => {

    const { buildingData, startQuizClicked } = useContext(AppContext)

    startQuizClicked(false)

    return (
        <div className="container">
            <div className="row">
                {buildingData.map(item => <BuildingItem key={item.id} item={item} />)}
            </div>
        </div>
    )

}
export default BuildingHome