import React, { Component } from 'react';
import { Route } from 'react-router';
import Home from './components/Home';
import { FetchData } from './components/FetchData';
import { Counter } from './components/Counter';
import './custom.css'
import ContextProvider from './components/Context/ContextProvider';
import ResultPage from './components/Result/ResultPage';
import NavMenu from './components/NavMenu';
import NewNavMenu from './components/NewNavMenu';
import QuizPage from './components/Quiz/QuizPage';
import CityHome from './components/Home/CityHome'
import BuildingHome from './components/Home/BuildingHome'
import WonderHome from './components/Home/WonderHome';

export default class App extends Component {
  static displayName = App.name;

  render () {
      return (
          <div>
            <ContextProvider>
            <NewNavMenu />
            <Route exact path='/' component={Home} />
            <Route path='/city' component={CityHome} />
            <Route path='/building' component={BuildingHome} />
            <Route path='/wonder' component={WonderHome} />
            <Route path='/quiz' component={QuizPage} />
            <Route path='/result' component={ResultPage} />
            <Route path='/fetch-data' component={FetchData} />
            </ContextProvider>
        </div>
             
    );
  }
}
