import React from 'react'

const Legend = () => {
    return (
        <div className="col-sm-4">
            <h5>Legend:</h5>
                <div className="row">
                    <div className="col-4">
                        <button className="btn btn-info">
                            <i className="fa fa-pencil"></i>
                        </button>
                        <p>Answered</p>
                    </div>
                    <div className="col-4">
                        <button className="btn btn-danger">
                            <i className="fa fa-question-circle"></i>
                        </button>
                        <p>Unanswered</p>
                    </div>
                </div>
            </div>
    )
}
export default Legend