import React from 'react'


const Legend = () => {
    return (
        <div className="col-sm-4">
            <h5>Legend:</h5>
            <div className="row">
                <div className="col-4">
                    <button className="btn btn-info">
                        <i className="fa fa-check"></i>
                    </button>
                    <p>Correct</p>
                </div>
                <div className="col-4">
                    <button className="btn btn-danger">
                        <i className="fa fa-times"></i>
                    </button>
                    <p>Incorrect</p>
                </div>
            </div>
        </div>
    )
}
export default Legend