import React, { useContext } from 'react'
import '../css/Answer.css'
import { AppContext } from '../Context/ContextProvider'
import ResultAnswer from './ResultAnswer'
import { Link } from 'react-router-dom'

const ResultQuesArea = () => {
    const { quizs, activeQues } = useContext(AppContext)
    return (
            <div>
            <h3>Question:</h3>  
                <div className="card card-body bg-light">
                    <div className="row mb-3">
                        <div className="col-sm-12">
                            <h4>{`${activeQues + 1}. ${quizs[activeQues].text}`}</h4>
                        </div>
                    </div>
                    <div className="row">
                    {quizs[activeQues].possibilities.map(
                        ans => <ResultAnswer ques={quizs[activeQues]}
                                key={quizs[activeQues].possibilities.indexOf(ans)}
                                indexQues={quizs.indexOf(quizs[activeQues])}
                                indexAns={quizs[activeQues].possibilities.indexOf(ans)}/>
                        )}
                    </div>
                    <Link to='/' style={{ textDecoration: "none" }}><button className="btn btn-primary">Back to facts</button></Link>
                </div>
            </div>
    )
}
export default ResultQuesArea