import React, { useContext } from 'react'
import { AppContext } from '../Context/ContextProvider'
import CityItem from '../FactCategory/CityItem'

const CityHome = () => {

    const { cityData, startQuizClicked } = useContext(AppContext)

    startQuizClicked(false)

    return (
        <div className="container">
            <div className="row">
                {cityData.map(item => <CityItem key={item.id} item={item} />)}
            </div>
        </div>
    )

}
export default CityHome