import React, { useContext } from 'react'
import Legend from './Legend'
import ResultProgressBar from './ResultProgressBar'
import ResultQuesArea from './ResultQuesArea'
import { AppContext } from '../Context/ContextProvider'
import 'font-awesome/css/font-awesome.min.css'

const ResultPage = () => {
    const { quizs } = useContext(AppContext)
    const calScore = () => {
        let i = 0
        quizs.map(item => {
            if (item.correct == true)
                i++
        })
        return i
    }
    return (
        <div className="container">
            <div className="row">
                <ResultProgressBar />
                <Legend />
            </div>
            <div className="row">
                <div className="col-md-12">
                    <h3>Your score: {calScore()}/{quizs.length}</h3>
                    <h3>Percentage: {calScore() / quizs.length * 100}%</h3>
                </div>
            </div>
            <div className="row">
                <div className="col-sm-12">
                    <ResultQuesArea />
                </div>
            </div>
        </div>
    )
}
export default ResultPage