import React, { useContext } from 'react'
import { UncontrolledCarousel } from 'reactstrap'
import { AppContext } from './Context/ContextProvider'
import CityItem from './FactCategory/CityItem'
import cityItems from './Carousel/CityCarousel'
import buildingItems from './Carousel/BuildingCarousel'
import wonderItems from './Carousel/WonderCarousel'
import './css/Carousel.css'

const Home = () => {
    
    const { data, startQuizClicked } = useContext(AppContext)
    
    startQuizClicked(false)
    
    return (
        <div className="container">
            <div className="row">
                {/*data.map(item => <CityItem key={item.id} item={item} />)*/}
                <div className="col-md-6 mb-5">
                    <h4><a href="/city" style={{ textDecoration: "none" }}>Learn about cities ....</a></h4>
                    <UncontrolledCarousel items={cityItems} />
                </div>
                <div className="col-md-6 mb-5">
                    <h4><a href="/wonder" style={{ textDecoration: "none" }}> Learn about 7 wonders of the world ....</a></h4>
                    <UncontrolledCarousel items={wonderItems} />
                </div>
                <div className="col-md-6 mb-5">
                    <h4><a href="/building" style={{ textDecoration: "none" }}> Learn about buildings ....</a></h4>
                    <UncontrolledCarousel items={buildingItems} />
                </div>
            </div>
      </div>
    )
  
}
export default Home