import React, { useContext } from 'react'
import { Link } from 'react-router-dom'
import '../css/Answer.css'
import { AppContext } from '../Context/ContextProvider'
import Answer from './Answer'

const QuesArea = () => {
    const { quizs, activeQues, showActiveQues, completed, resetCompleted, resetActiveQues } = useContext(AppContext)
    return (
        !completed ?
            <div>
                <h3>Question:</h3>
            <div className="card card-body bg-light">
                <div style={{ marginBottom: 15 }} className="row">
                    <div className="col-sm-12">    
                        <h4>{`${activeQues + 1}. ${quizs[activeQues].text}`}</h4>
                    </div>
                </div>
                <div className="row">
                        {quizs[activeQues].possibilities.map(
                            ans => <Answer ques={quizs[activeQues]}
                                key={quizs[activeQues].possibilities.indexOf(ans)}
                                index={quizs[activeQues].possibilities.indexOf(ans)} />
                        )}
                </div>
                <button className="btn btn-warning" onClick={() => showActiveQues(activeQues+1)}>Continue</button>
            </div>
            </div>

            :
                <div className="card card-body bg-light">
                    <div className="row">
                        <div className="col-sm-12">
                        <h4>Are you sure you want to submit your answer?</h4>
                        <button className="btn btn-success mr-2" onClick={resetActiveQues}><Link to='./result' style={{ textDecoration: "none", color:"white" }}>Yes</Link></button>
                            <button className="btn btn-danger" onClick={resetCompleted}>No</button>
                        </div>
                    </div>
                </div>
    )
}
export default QuesArea