import React, { useState, useContext } from 'react'
import '../css/Image.css'
import { AppContext } from '../Context/ContextProvider'
import WonderModalInfo from '../ModalInfos/WonderModalInfo'

const WonderItem = ({ item }) => {
    const { showModal } = useContext(AppContext)

    return (
        <React.Fragment>
            <div style={{ marginBottom: 25 }} className="col-sm-6">
                <div className="card card-body bg-light">
                    <div className="row">
                        <div className="col-md-6">
                            <img src={item.image_url} className="rounded img-fluid card-image" />
                        </div>
                        <div className="col-md-6">
                            <h4>{item.type}</h4>
                            <p><strong>Location: </strong>{item.locations}</p>
                            <p><strong>Built: </strong>{item.built}</p>
                            <button className="btn btn-primary float-right" onClick={() => showModal(item)}>Learn more</button>
                        </div>
                    </div>
                </div>
            </div>
            <WonderModalInfo />
        </React.Fragment>
    )
}

export default WonderItem