import React, { useContext } from 'react'
import { AppContext } from '../Context/ContextProvider'
import WonderItem from '../FactCategory/WonderItem'

const WonderHome = () => {

    const { wonderData, startQuizClicked } = useContext(AppContext)

    startQuizClicked(false)

    return (
        <div className="container">
            <div className="row">
                {wonderData.map(item => <WonderItem key={item.id} item={item} />)}
            </div>
        </div>
    )

}
export default WonderHome