import React, { createContext, useState, useEffect } from 'react'
import turtlesData from '../Data/TurtleData'
import quizQuestions from '../Data/Wonder/WonderQuizQuestion'
import answerData from '../Data/AnswerData'
import getBuildingData from '../Data/BuildingData'
import getCityData from '../Data/CityData'
import getWonderData from '../Data/Wonder/WonderData'
export const AppContext = createContext()

const ContextProvider = (props) => {

    const [buildingData, setBuilding] = useState(getBuildingData)
    const [cityData, setCity] = useState(getCityData)
    const [wonderData, setWonderData] = useState(getWonderData)
    const [data, setData] = useState(getCityData)
    const [quizs, setQuizs] = useState(quizQuestions)
    const [ansData, setAnsData] = useState(answerData)
    const [activeQues, setActiveQues] = useState(0)
    const [completed, setCompleted] = useState(false)
    const [startQuiz, setStartQuiz] = useState(false)

    useEffect(() => {

       
    }, [])
    const [isOpen, setIsOpen] = useState(false)
    const showActiveQues = (number) => {
        if (number > quizs.length - 1) {
            number = quizs.length - 1
        }
        else {
            setActiveQues(number)
        }    
    }
    const toggle = () => setIsOpen(!isOpen)
    const [item, setItem] = useState({})
    const showModal = (item) => {
        setItem(item)
        setIsOpen(true)
    }
    const selectAns = (index) => {
        const newList = quizs.map((item) => {
            if (quizs.indexOf(item) === activeQues) {
                const updatedItem = {
                    ...item,
                    selected: index,
                }

                return updatedItem;
            }
            return item;
        })
        setQuizs(newList)

        let unAnswered = newList.filter(item => item.selected == null)
        if (unAnswered.length == 0) {
            setCompleted(true)
        }
    }

    const updateCorrectAnswer = () => {
        const newList = quizs.map((item) => {
            if (item.selected === ansData[quizs.indexOf(item)]) {
                const updatedItem = {
                    ...item,
                    correct: true
                }
                return updatedItem
            }
            else {
                const updatedItem = {
                    ...item,
                    correct: false
                }
                return updatedItem
            }
            return item
        })
        setQuizs(newList)
    }

    const resetCompleted = () => {
        setCompleted(false)
    }

    const resetActiveQues = () => {
        updateCorrectAnswer()
        setActiveQues(0)
    }

    const startQuizClicked = (flag) => {
        console.log("Start clicked", flag)
        setStartQuiz(flag)
    }
    
    return (
        <AppContext.Provider value={{
            data, isOpen, toggle, item, showModal, quizs, activeQues,
            showActiveQues, selectAns, completed, resetCompleted, ansData,
            resetActiveQues, startQuizClicked, startQuiz, cityData, buildingData, wonderData
        }}>
            {props.children}
        </AppContext.Provider>
        )
}

export default ContextProvider