import React, { useContext } from 'react'
import '../css/Answer.css'
import '../css/Image.css'
import { AppContext } from '../Context/ContextProvider'

const Answer = ({ ques, index }) => {
    const { selectAns, activeQues } = useContext(AppContext)
    return (
        
        ques.type == "text" ?
        <div className="col-sm-6">
                <h4 className={ques.selected === index ? 'answer bg-info' : 'answer'}
                    onClick={() => selectAns(index)}>
                {ques.possibilities[index].answer}</h4>
        </div>
         :
            <div className="col-sm-6">
                <div className={ques.selected === index ? 'image-answer image-selected' : 'image-answer'}
                onClick={() => selectAns(index)}>
                    <img src={ques.possibilities[index].answer} />
                </div>
            </div>
    )
}
export default Answer