import React, { useContext } from 'react'
import { AppContext } from '../Context/ContextProvider'


const ProgressBar = () => {
    const { quizs, showActiveQues } = useContext(AppContext)
    return (
            <div className="col-sm-8">
                <h2>Progress:</h2>
                <div className="btn-toolbar">
                {quizs.map(x => <button className={x.selected === null ? "btn btn-danger ml-2 mb-2" : "btn btn-info ml-2 mb-2"}
                                onClick={() => showActiveQues(quizs.indexOf(x))}>
                                <i className={x.selected === null ? "fa fa-question-circle" : "fa fa-pencil"}></i>
                               </button>
                    )}
                </div>
            </div> 
    )
}
export default ProgressBar